
import { mapState, mapActions } from "vuex";
import Rater from 'vux/src/components/rater/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import vHeader from "@/components/v-header";
const qs = require("qs");
export default {
data() {
return{
id: this.getUrlParam("id"),
className: this.getUrlParam("className"),
urlPic:this.api.urlPic,
boxShow:false,
storeName:'',
totalPage: 1,
currentPage: 0,
listData:[],
loadMoreVal:true,
pullupEnabled: true,
status: {
pullupStatus: 'default'
}
}
},
components: {
vHeader,
Scroller,
Rater,
Spinner,
Flexbox,
FlexboxItem
},
computed: {
...mapState(["token","city","location"]),
},
mounted(){

},
created() {
this.loadMore();
},
methods:{
loadMore () {
if(this.loadMoreVal){
this.loadMoreVal=false;
setTimeout(()=>{
this.loadMoreVal=true;
},1000)
}else{
return;
}
console.log(this.location);
if(this.currentPage< this.totalPage){
this.currentPage= parseInt(this.currentPage) + 1;
this.$axios.post(this.api.sellerstoreList,qs.parse({'sellerClassId':this.id,'latitude':this.location.lat,'longitude':this.location.lng,"page" : this.currentPage, "limit":8 }),{headers: {"content-type": "application/json"}})
.then(res => {
console.log(res.data);
this.totalPage=res.data.content.totalPage;
this.listData = [...this.listData,...res.data.content.list];
})
.catch(res => {

});
}else{
this.status.pullupStatus = 'complete';
setTimeout(()=>{
this.status.pullupStatus = 'default';
},1000);
console.log('已加载完');
}
setTimeout(() => {

}, 10)
},
refresh () {
console.log('刷新');
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
},
toDetail (id) {
console.log(21);
this.$router.push({//核心语句
path:'/store',//跳转的路径
query:{//路由传参时push和query搭配使用 ，作用时传递参数
id: id
}
})
}
}
};



